import React,{Component} from 'react';
import {connect}from 'react-redux';
import {NavBar,List,InputItem,Icon,Grid} from 'antd-mobile';
import {sendMsg,updateMsg}from '../../redux/actions'
import QueueAnim from 'rc-queue-anim'
const Item=List.Item;
 class Chat extends Component{
     state={
         content:''
     };
     // 切换表情列表的显示
     toggleShow = () => {
         const isShow = !this.state.isShow
         this.setState({isShow})
         if(isShow) {
             // 异步手动派发resize事件,解决表情列表显示的bug
             setTimeout(() => {
                 window.dispatchEvent(new Event('resize'))
             }, 0)
         }
     }

     send=()=>{
     //    发送消息，请求
         const {content}=this.state;
         const from=this.props.user._id;
         const to=this.props.match.params.userid;
         // console.log(from,to)
         this.props.sendMsg({from,to,content})
     //    清空输入消息
         this.setState({
             content:"",
             isShow:false
         })

     }
    //列表自动滑动到底部
     componentDidMount() {
         // 初始显示列表
         window.scrollTo(0, document.body.scrollHeight);
         const from=this.props.match.params.userid;
         const to=this.props.user._id;
         this.props.updateMsg(from,to)

     }
     // 退出()死亡之前调用
     componentWillUnmount() {
         const from = this.props.match.params.userid
         const to = this.props.user._id
         this.props.updateMsg(from, to)
     }
     componentDidUpdate () {
         // 更新显示列表
         window.scrollTo(0, document.body.scrollHeight)
     }
     componentWillMount () {// 第一次调用render渲染前调用, 调用一次
         const emojis = ['😀', '😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣','😀'
             ,'😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣'
             ,'😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣'
             ,'😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣','😀', '😁', '🤣']
         this.emojis=emojis.map(value=>({text:value}))
     }
     render(){
         const {user,chat}=this.props;
        // console.log(chat)
         const {users,chatMsgs}=chat;
        //得到目标用户的id
        const targetId=this.props.match.params.userid;
        /* console.log(targetId);
        console.log(users);*/
         const meId=user._id;
         // console.log(user._id)
        if(!users[targetId]) {
            return null // 不做任何显示
        }
         const targetUser=users[targetId]
        const targetIcon=targetUser.header ? require(`../../assets/img/${targetUser.header}.png`):null
        const targetUsername=targetUser.username;
        // 与目标用户聊天
        //我发给他的，他发给我的都要显示出来
        const chatId=[targetId,meId].sort().join('_');
       // 要显示的聊天信息（与目标用户）
       const currentMsgs= chatMsgs.filter(msg=>msg.chat_id===chatId)


        return (
            (
                <div id='chat-page'>
                    <NavBar className='mainNavList' icon={<Icon type='left'/>}
                            onLeftClick={()=>this.props.history.goBack()
                            }
                    >{targetUsername}</NavBar>
                    <List style={{marginBottom: 50}}>
                        <QueueAnim type='scale' delay={100}>
                            {
                                currentMsgs.map((msg, index) => {
                                    if(msg.to===meId) { // 对方发给我的
                                        return (
                                            <Item
                                                key={index}
                                                thumb={targetIcon}
                                            >
                                                {msg.content}
                                            </Item>
                                        )
                                    } else { // 我发给对方的
                                        return (
                                            <Item
                                                key={index}
                                                className='chat-me'
                                                extra='我'
                                            >
                                                {msg.content}
                                            </Item>
                                        )
                                    }
                                })
                            }
                        </QueueAnim>
                    </List>



                    <div className='am-tab-bar'>
                        <InputItem
                            placeholder="请输入"
                            onFocus={()=>{this.setState({isShow:false})}}
                            onChange={(val)=>this.setState(
                                {
                                    content:val
                                }

                                )}
                            value={this.state.content}
                            extra={
                                <div>
                                    <span style={{marginLeft:15}} onClick={this.toggleShow}>😁</span>
                                    <span onClick={this.send}>发送</span>


                                </div>

                            }


                        />
                        {
                            this.state.isShow ? (
                                <Grid
                                    data={this.emojis}
                                    columnNum={8}
                                    carouselMaxRow={4}
                                    isCarousel={true}
                                    onClick={(item) => {
                                        this.setState({content: this.state.content + item.text})
                                    }}
                                />
                            ) : null
                        }
                    </div>
                </div>
            )

        )
    }
}
export default connect(
state=>({user:state.user,chat:state.chat}),
    {sendMsg,updateMsg}
)(Chat)


