import React, {Component} from 'react';
import {List,Badge}from 'antd-mobile'
import {connect} from 'react-redux';
const Item=List.Item;
const Brief=Item.Brief;
 //根据chatMsg得到所有聊天的最后聊天msg的数组
 //1，创建一个用于保存所有lastMsg的对象容器：lastMsgObjs
//遍历每个msg，判断msg是否是它对应的聊天lastMsg,如果是放入
//得到lastMsgObjs中所有属性值组成的数组：lastMSG
//排序
function getLastMags(chatMsgs,meId) {
    // console.log(chatMsgs)
//    创建一个空对象，用于保存所有lastMsg的容器对象
    const lastMsgObjs={};
//    遍历每个msg，判断msg是否是它对应聊天的lastMsg,如果是放入
    chatMsgs.forEach(msg=>{

       // 统计自身的unReadCount
        if(!msg.read&&msg.to===meId){
            msg.unReadCount=1;
        }else{
            msg.unReadCount=0
        }
       const chatId=msg.chat_id;
       // console.log(chatId)
       const lastMsg=lastMsgObjs[chatId];

       if(!lastMsg){
           //当前聊天还没有lastMsg
           lastMsgObjs[chatId]=msg;
       }else {
           //当前聊天已有lastMsg
           const unReadCount=lastMsg.unReadCount+msg.unReadCount;
           if(msg.create_time>lastMsg.create_time){
               lastMsgObjs[chatId]=msg;
           }
           lastMsgObjs[chatId].unReadCount=unReadCount

       }

    })
    // 3. 得到lastMsgObjs中所有属性值组成的数组: lastMsgs
    const lastMsgs = Object.values(lastMsgObjs)
    lastMsgs.sort(function (m1,m2) {
        return m2.create_time-m1.create_time
    })
    console.log(lastMsgs)
    return lastMsgs;
}
 class Message extends Component {
    render() {
        const {user,chat}=this.props;
        const {users,chatMsgs}=chat;
        const meId=this.props.user._id;
          const lastMsgs =getLastMags(chatMsgs,meId)
        return (

              <div>
                  <List style={{marginTop:50,marginBottom:50}}>
                      {
                          lastMsgs.map((msg,index)=>{
                              const targetId=msg.from===meId ? msg.to:msg.from;
                              const targetUser=users[targetId];
                              const targetUsername=targetUser.username

                              const targetIcon=targetUser.header ? require(`../../assets/img/${targetUser.header}.png`):null;
                              return (
                                  <Item
                                      key={index}
                                      extra={<Badge text={msg.unReadCount}/>}
                                      thumb={targetIcon}
                                      arrow='horizontal'
                                      onClick={()=>{this.props.history.push(`/chat/${targetId}`)}}
                                  >
                                      {msg.content}
                                      <Brief>{targetUsername}</Brief>
                                  </Item>

                              )
                          })
                      }



                  </List>
              </div>
        )
    }
    }

export default connect(
    state=>({user:state.user,chat:state.chat}),
    {}
)(Message)


