import React,{Component} from 'react';
import {connect} from 'react-redux'
import {NavBar,Button,InputItem,TextareaItem} from 'antd-mobile'
import HeaderSelector from '../../components/header-selector/header-selector'
import {updateUser} from '../../redux/actions'
import {Redirect}from 'react-router-dom'
 class LaoBanInfo extends Component{
    state={
        header:'',//用户头像的文件名
        post:"",
        info:"",
        company:"",
        salary:''

    }
     handleChange=(name,val)=>{
     //   更新状态
         this.setState({
             [name]:val
         })

     };
    //设置更新header
     setHeader=(header)=>{
       this.setState({
           header
       })
     };
    save=()=>{
        this.props.updateUser(this.state)
    };
 render(){
     const {header}=this.props.user;
    if(header){
        return <Redirect to='/laoban'/>
    }
     return (
         <div>
             <NavBar>老板信息完善</NavBar>
             <HeaderSelector setHeader={this.setHeader} />
             <InputItem placeholder='招聘职位' onChange={(val)=>this.handleChange('post',val)}>招聘职位</InputItem>
             <InputItem placeholder='公司名称' onChange={(val)=>this.handleChange('company',val)}>公司名称</InputItem>
             <InputItem placeholder='职位薪资' onChange={(val)=>this.handleChange('salary',val)}>职位薪资</InputItem>
             <TextareaItem placeholder='职位要求' title="职位要求"
                           onChange={(val)=>this.handleChange('info',val)}
                           rows={3}/>
             <Button type="primary" onClick={this.save}>保存</Button>
         </div>

     )
 }
}
export default connect(
    state => ({user: state.user}),
    {updateUser}
)(LaoBanInfo)


